const app = getApp()
import helper from '../common/helper.js';
export default {
	data() {
		return {
			isCheck: false,
			bannerIndex: '0',
			current: '0',
			vip: [],
			imgUrl: '',
			token: '',
			money: ''
		}
	},
	onLoad() {
		this.token = uni.getStorageSync('token')
		this.vipList()
		this.imgUrl = helper.imgUrl
	},
	onShow() {
		// console.log(app.globalData.vipIndex)
		if(app.globalData.vipIndex == 1){
			this.isCheck = true
		}
	},
	methods: {
		getMony(e){
			console.log(e,'************')
			this.current =e.detail.value
		},
		meTreaty(){
			uni.navigateTo({
				url:'/pages/meTreaty'
			})
		},
		banner(e){
			// this.current = e.detail.current
			// console.log(e)
		},
		checkboxChange(e){
			this.isCheck = !this.isCheck
		},
		vipList(){	
			uni.request({
				url: `${helper.requestUrl}/vips`,
				method: 'GET',
				header: {
					authorization: this.token
				},
				success: res => {
					uni.hideLoading();
					res = helper.null2str(res)
					console.log(res)
					if (res.data.status_code == 200) {
						this.vip = res.data
					} else {
						uni.showToast({
							title: res.data.message
						});
					}
			
				}
			})
		},
		goVip(e){
			console.log(this.isCheck)
			if (!this.isCheck) {
				uni.showToast({
					title: '请同意相关协议',
					icon: 'none',
					duration: 2000
				});
			}else{
				uni.navigateTo({
					url:`/pages/payType?id=${e.currentTarget.dataset.id}&money=${e.currentTarget.dataset.money}`
				})
			}
			// let money = this.money
			// if (money != '') {
			// 	// console.log(e)
			// 	uni.navigateTo({
			// 		url:`/pages/payType?id=${e.currentTarget.dataset.id}&money=${money}`
			// 	})
			// } else {
			// 	uni.showToast({
			// 		title: '请选择价格',
			// 		icon: 'none',
			// 		duration: 2000
			// 	});
			// }
			
		}
	}
}
